import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { User, UserRegister, UserLogin } from './models/user';
import { Observable, throwError } from 'rxjs';
import { catchError } from 'rxjs/operators';
import { Router } from '@angular/router';

const URL = "https://api.movin.tk/api/auth";

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private _usuario: User | null | undefined;
  private _token: string | null | undefined;
  private httpHeader = new HttpHeaders({'Content-Type':'application/json'})

  constructor(private http: HttpClient, private router: Router) { }

  public get usuario(): User {
        if(this._usuario != null){
          return this._usuario
        } else if(this._usuario == null && localStorage.getItem('usuario') != null) {
          this._usuario = JSON.parse(localStorage.getItem('usuario')!) as User
          return this._usuario
        } else {
          return new User();
        }
    }


  public get token() {
      if(this._token != null){
          return this._token
      } else if(this._token == null && localStorage.getItem('access_token') != null) {
          this._token = localStorage.getItem('access_token')!
          return this._token
      } else {
          return null
      }
  }

  private addAuthHeaders(): HttpHeaders{
    let token = this.token;
    if(token != null){
      return this.httpHeader.append('Authorization','Bearer ' + token)
    } else {
      return this.httpHeader
    }
  }

  register(user: UserRegister): Observable<any>{
    return this.http.post<any>(URL + "/signup", user)
  }

  login(user:UserLogin): Observable<any>{
    return this.http.post<any>(URL + "/signin", user)
  }

  logout() {
    this._usuario = null;
    this._token = null;
    localStorage.removeItem('access_token');
    localStorage.removeItem('usuario');
  }

  guardarToken(access_token: string): void {
    this._token = access_token;
    localStorage.setItem('access_token', access_token)
  }

  guardarUsuario(user: User){
    localStorage.setItem('usuario', JSON.stringify(user));
  }

  whoAmI(): Observable<any>{
    return this.http.get<any>(URL + "/signin/whoami", {headers: this.addAuthHeaders()}).pipe(
      catchError( err => {
          this.isNotAuth(err)
          return throwError(err)
      })
    )
  }

  private isNotAuth(e: { status: number; }): boolean{
    if(e){
      localStorage.removeItem('access_token');
      localStorage.removeItem('usuario');
        return true
    }
  }

}
